* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

#root {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: min(90%, 600px);
  height: min(90%, 800px);
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px;
  margin: 0 auto;
  width: 100%;
  height: 90px;
  border: 1px solid #000;
}
header h1 {
  width: 200px;
  height: 100%;
}
header h1 img {
  width: 100%;
  height: 100%;
}
header nav {
  width: 45%;
}
@media screen and (min-width: 600px) {
  header nav > ul {
    display: flex;
    justify-content: flex-end;
  }
}
@media screen and (max-width: 600px) {
  header nav > ul > li {
    float: right;
  }
  header nav > ul > li.user {
    display: none;
  }
}
header nav > ul > li.user {
  margin: auto;
}
header nav > ul > li.user small {
  font-weight: 600;
}
header nav > ul > li a {
  padding: 10px;
  background: rgba(0, 0, 0, 0.8);
  font-weight: 600;
  text-decoration: none;
  color: #FFF;
  border: none;
  border-radius: 5px;
  transition: all 0.3s;
}
header nav > ul > li a:hover {
  background: rgba(0, 0, 0, 0.2);
  color: #000;
}
header nav > ul > li button {
  padding: 10px;
  background: rgba(0, 0, 0, 0.8);
  font-weight: 600;
  color: #FFF;
  border: none;
  border-radius: 5px;
  transition: all 0.3s;
}
header nav > ul > li button:hover {
  background: rgba(0, 0, 0, 0.2);
  color: #000;
}

form {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 10px;
  width: 100%;
  border: 1px solid #000;
  border-top: none;
  text-align: center;
}
form > p {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 15px;
  width: 100%;
}
form > p input {
  padding: 10px;
  border: 1px solid #000;
  border-radius: 10px;
}
form > p input:focus {
  background: #000;
  color: #FFF;
}
form > p button {
  padding: 10px 0;
  width: 100%;
  background: #FFF;
  border: 1px solid #000;
  border-radius: 10px;
}
form > p button:hover, form > p button:focus {
  background: #000;
  color: #FFF;
}
@media screen and (min-width: 600px) {
  form > p small {
    width: min(15%, 65px);
    margin-right: 5px;
  }
  form > p input {
    flex: 1;
  }
}
@media screen and (max-width: 600px) {
  form > p {
    padding: 5px;
    flex-wrap: wrap;
  }
  form > p small {
    width: 100%;
  }
  form > p input {
    width: 100%;
  }
}

section {
  border: 1px solid #000;
  border-top: none;
}
section ul {
  padding: 50px 0;
  margin: 0 auto;
  width: min(90%, 500px);
}
section ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  margin-bottom: 15px;
  border: 1px solid #000;
}

footer {
  box-sizing: border-box;
  padding: 10px 0;
  width: 100%;
  border: 1px solid #000;
  border-top: none;
  text-align: center;
}
footer p {
  width: 200px;
  border: 1px solid #000;
  margin: 0 auto;
}
footer p:nth-child(2) {
  margin: 10px auto;
}/*# sourceMappingURL=style.css.map */