*{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
#root{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: min(90%,600px);
  height: min(90%, 800px);
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px;
  margin: 0 auto;
  width: 100%;
  height: 90px;
  border: 1px solid #000;
  h1{
    width: 200px;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
  nav{
    width:45%;
    >ul{
      @media screen and (min-width:600px) {
        display: flex;
        justify-content: flex-end;
      }
      @media screen and (max-width:600px) {
        >li{
          &.user{display: none;}
          float: right;
        }
      }
      >li{
        &.user{
          margin: auto;
          small{
            font-weight: 600;
            
          }
        }
        a{
          padding: 10px;
          background: rgba(#000,0.8);
          font-weight: 600;
          text-decoration: none;
          color: #FFF;
          border: none;
          border-radius: 5px;
          transition: all .3s;
          &:hover{
            background: rgba(#000,0.2);
            color: #000;
          }
        }
        button{
          padding: 10px;
          background: rgba(#000,0.8);
          font-weight: 600;
          color: #FFF;
          border: none;
          border-radius: 5px;
          transition: all .3s;
          &:hover{
            background: rgba(#000,0.2);
            color: #000;
          }
        }
      }
    }
  }
}

form{
  box-sizing: border-box;
  margin: 0 auto;
  padding: 10px;
  width: 100%;
  border: 1px solid #000;
  border-top: none;
  text-align: center;
  >p{
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 15px;
    width: 100%;
    input{
      padding: 10px;
      border: 1px solid #000;
      border-radius: 10px;
      &:focus{
        background: #000;
        color: #FFF;
      }
    }
    button{
      padding: 10px 0;
      width: 100%;
      background: #FFF;
      border: 1px solid #000;
      border-radius: 10px;
      &:hover,&:focus{
        background: #000;
        color: #FFF;
      }
    }
    @media screen and (min-width:600px) {
      small{
        width: min(15%,65px);
        margin-right: 5px;
      }
      input{
        flex: 1;
      }
    }
    @media screen and (max-width:600px) {
      padding: 5px;
      flex-wrap: wrap;
      small{
        width: 100%;
      }
      input{
        width: 100%;
      }
    }
  }
}

section{
  border: 1px solid #000;
  border-top: none;
  ul{
    padding: 50px 0;
    margin: 0 auto;
    width: min(90%,500px);
    li{
      display: flex;
        justify-content: space-between;
        align-items: center;
      padding: 10px 15px;
      margin-bottom: 15px;
      border: 1px solid #000;
    }
  }
}
footer{
  box-sizing: border-box;
  padding: 10px 0;
  width: 100%;
  border: 1px solid #000;
  border-top: none;
  text-align: center;
  p{
    width: 200px;
    border: 1px solid #000;
    margin: 0 auto;
    &:nth-child(2){
      margin: 10px auto;
    }
  }
}